:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
}

body {
  background: var(--bg-gray-100);
}

.App {
  min-height: 100vh;
  color: var(--text-primary);
  background: var(--bg-gray-100);
}

.form-container {
  max-width: 500px;
  margin: 2.5rem auto 1.5rem;
  padding: 2rem;
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-xl);
  background: var(--bg-white);
}

label {
  display: block;
  font-size: 0.93rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
  letter-spacing: 0.025em;
}

input, textarea, select {
  width: 100%;
  height: 2.5rem;
  padding: 0.5rem;
  border: 1px solid var(--border-light);
  border-radius: var(--radius-md);
  font-size: 1rem;
  transition: all 0.2s ease;
}
input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: var(--border-focus);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.10);
}

button {
  min-width: 80px;
  min-height: 2.3rem;
  font-size: 1rem;
  border-radius: var(--radius-md);
  font-weight: 500;
}

.btn-primary {
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
}
.btn-primary:hover {
  background: #2563eb;
  transform: translateY(-1px);
}
.btn-secondary {
  background: transparent;
  color: var(--accent);
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  transition: all 0.2s ease;
}
.btn-secondary:hover {
  background: var(--accent);
  color: white;
}

.error {
  border-color: #ef4444 !important;
  color: #dc2626;
  font-size: 0.95rem;
  margin-top: 3px;
}
.empty-state {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  padding: 2rem 0;
  font-size: 1.05rem;
}
:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}

@media (max-width: 360px) {
  .form-container {
    padding: 1rem;
    margin: 0.5rem;
    max-width: 99vw;
  }
  input, textarea, select {
    padding: 0.375rem;
    font-size: 0.9rem;
  }
}
